.anim div
{
    border-radius: 25%;
    box-shadow: 1px 2px 1px 1px rgba(0, 0, 0, 0.4);
    position: absolute;
    transition: all 0.9s;
    top : 20%;
    left: 180px;
}
.anim 
{
  top:70px;
  background-color:#fff;
  border-radius:2px;
  height:100px;
  padding:10px;
  width:320px;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.4);
  margin-left:10px;
  margin-right:auto;

}
.wrapper{
  width: 100%;
  height: 100vh;
  text-align: center;
}
.wrapper form{
  position: absolute;
  bottom: 0;
  left: 40%;
  margin-bottom: 30px;
}